import axios from "axios";

export default (token) => {
  // console.log(process.env.HOST+':'+process.env.PORT)
  if(token){
    return axios.create({
        baseURL: "http://127.0.0.1:8000/",
        // timeout: 1000,
        headers: { Authorization: "Token "+token},
      });
  }
  return axios.create({
    baseURL: "http://127.0.0.1:8000/",
    // timeout: 1000,
    // headers: { Authorization: "Token ${token}" },
  });
};
